<template>
  <div class="videosList">
    <swiper class="swiperBox" :options="swiperOption">
      <swiper-slide class="swiper-slide" v-for="(item, index) in videoList" :key="item.id">
        <!--  -->
        <div>
          <VideoPlay ref="videos" :videoList="videoList[vi] || item" :index="index"></VideoPlay>
        </div>
        <!--  -->
        <div class="video-info">
          <video-info :videoList="item"></video-info>
        </div>
        <!--  -->
        <div class="right">
          <right-nav
            :isClickRight="isClickRight"
            @clickRight="clickRight"
            :videoList="item"
            :comment="comment"
            @changeCommentOpen="changeCommentOpen"
          ></right-nav>
        </div>
      </swiper-slide>
    </swiper>
    <!--  -->
    <transition name="up">
      <div v-if="comment" class="comment-info">
        <Comment :comment="comment" @changeCommentClose="changeCommentClose"></Comment>
      </div>
    </transition>
  </div>
</template>

<script>
import { Swiper, SwiperSlide } from "vue-awesome-swiper";
import VideoPlay from "@/components/VideoPlay.vue";
import VideoInfo from "@/components/VideoInfo.vue";
import RightNav from "@/components/RightNav.vue";
import Comment from "@/components/Comment.vue";

export default {
  name: "Videos",
  components: {
    Swiper,
    SwiperSlide,
    VideoPlay,
    VideoInfo,
    RightNav,
    Comment,
  },
  props: ["videoList", "videoIndex"],
  data() {
    return {
      vi: this.videoIndex,
      page: 1,
      // 
      swiperOption: {
        direction: "vertical", //
        grabCursor: true, //Swiper
        setWrapperSize: true, //Swiperflexbox(display: flex)Wrapperslidesflexbox
        autoHeight: true, //truewrappercontainerslide
        slidesPerView: 1, //sliderslides(carousel)loop 'auto'slidesloop'auto'loopedSlides
        mousewheel: true, //Swiperfalse
        mousewheelControl: true, //
        height: window.innerHeight, // 
        resistanceRatio: 0, //slide0
        observeParents: true, //observeSwiperSwiperwindow.resizeSwiper

        on: {
          // 
          tap: () => {
            this.isClickRight = false;
            this.playAction(this.page - 1);
          },
          //
          slideNextTransitionStart: () => {
            this.page += 1;
            this.nextVideo(this.page - 1);
          },
          // 
          slidePrevTransitionEnd: () => {
            if (this.page > 1) {
              this.page -= 1;
              this.prevVideo(this.page - 1);
            }
          },
        },
      },
      comment: false,
      isClickRight: false,
    };
  },
  methods: {
    // 
    clickRight(value) {
      this.isClickRight = value;
    },
    // 
    playAction(index) {
      setTimeout(() => {
        // 
        if (!this.isClickRight) {
          this.$refs.videos[index].playOrStop();
        }
      }, 100);
    },
    // 
    prevVideo(index) {
      this.comment = false;
      // 
      this.$refs.videos[index + 1].stop();
      this.$refs.videos[index].play();
    },
    // 
    nextVideo(index) {
      this.comment = false;
      // 
      this.$refs.videos[index - 1].stop();
      this.$refs.videos[index].play();
    },
    // 
    changeCommentOpen(value) {
      this.comment = value;
    },
    // 
    changeCommentClose(value) {
      this.comment = value;
      this.isClickRight = value;
    },
  },
};
</script>

<style lang="scss" scope>
.videosList {
  width: 100%;
  height: 100%;
  position: relative;

  .swiper-wrapper {
    width: 100%;
    height: 100%;
  }

  .swiperBox {
    width: 100%;
    height: 100%;
  }

  .swiper-slide {
    position: relative;

    div:nth-child(1) {
      height: 100%;
    }
  }

  .video-info {
    position: absolute;
    bottom: 60px;
    left: 10px;
    z-index: 999;
  }
  .right {
    position: absolute;
    width: 50px;
    bottom: 60px;
    right: 10px;
    z-index: 999;
  }
  .comment-info {
    position: absolute;
    width: 100%;
    height: 100%;
    bottom: 50px;
    left: 0;
    z-index: 999;
  }
}
