<template>
  <div>
    <video-player
      class="video-player vjs-big-play-button"
      ref="videoPlayer"
      :playsinline="true"
      :options="playerOptions"
    ></video-player>
  </div>
</template>

<script>
// 
import { videoPlayer } from "vue-video-player";
import "video.js/dist/video-js.css";
import "vue-video-player/src/custom-theme.css";

export default {
  name: "VideoPlay",
  components: {
    videoPlayer,
  },
  props: ["videoList", "index"],
  data() {
    return {
      //
      playerOptions: {
        autoplay: false, //
        muted: false, // 
        loop: true, // 
        preload: "auto", // <video>auto,
        fluid: true, // trueVideo.js player
        sources: [
          {
            type: "video/mp4", //git
            // src: require('../assets/video/01.mp4') //url,require()
            src: this.videoList.url,
          },
        ],
        width: document.documentElement.clientWidth - 100 + "px", //
        notSupportedMessage: "", //Video.js
        controlBar: false,
      },
      // 
      isPlay: true,
    };
  },
  created() {
    this.autoPlayAction();
  },
  methods: {
    // 
    playOrStop() {
      if (this.isPlay) {
        this.stop();
      } else {
        this.$refs.videoPlayer.player.play();
        this.isPlay = true;
      }
    },
    // 
    autoPlayAction() {
      if (this.index == 0) {
        this.playerOptions.autoplay = true;
      }
    },
    // 
    play() {
      this.$refs.videoPlayer.player.load(); //
      this.$refs.videoPlayer.player.play();
      this.isPlay = true;
    },
    // 
    stop() {
      this.$refs.videoPlayer.player.pause();
      this.isPlay = false;
    },
  },
};
</script>

<style lang="scss">
.video-player {
  position: relative;
  .video-js .vjs-big-play-button {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border: none;
    font-size: 600%;
    background-color: rgba(0, 0, 0, 0);
    span {
      position: absolute;
      top: 40%;
      left: 45%;
      opacity: 0.5;
    }
  }
}
</style>
